import React, { useState, useCallback, useEffect } from 'react';
import { useHistory } from 'react-router-dom';

import debounce from 'lodash/debounce';
import Select from 'react-select';
import makeAnimated from 'react-select/animated';
import { func, object } from 'prop-types';

const tempUzmanlik = [
  { value: 'Adli Psikoloji', label: 'Adli Psikoloji' },
  { value: 'Anksiyete (Kayg) Bozukluklar', label: 'Anksiyete (Kayg) Bozukluklar' },
  { value: 'Bipolar Bozukluk', label: 'Bipolar Bozukluk' },
  { value: 'ocuk ve Ergen Terapisi', label: 'ocuk ve Ergen Terapisi' },
  { value: 'letiim Problemleri', label: 'letiim Problemleri' },
  { value: 'fke Kontrol Problemleri', label: 'fke Kontrol Problemleri' },
  { value: 'TSSB (Travma-Stres)', label: 'TSSB (Travma-Stres)' },
  { value: 'Yas Danmanl', label: 'Yas Danmanl' },
  { value: 'Aile Terapisi', label: 'Aile Terapisi' },
  { value: 'Bamllk', label: 'Bamllk' },
  { value: 'Bireysel / Yetikin Terapisi', label: 'Bireysel / Yetikin Terapisi' },
  { value: 'Depresyon', label: 'Depresyon' },
  { value: 'Kiilik Bozukluklar', label: 'Kiilik Bozukluklar' },
  { value: 'Panik Bozukluk', label: 'Panik Bozukluk' },
  { value: 'Uyku Bozukluklar', label: 'Uyku Bozukluklar' }
];

const tempUnvani = [
  { value: '', label: ' ' },
  { value: 'Aile Danman', label: 'Aile Danman' },
  { value: 'Psikolog', label: 'Psikolog' },
  { value: 'Psikiyatrist', label: 'Psikiyatrist' },
  { value: 'Uzman Nropsikolog', label: 'Uzman Nropsikolog' },
  { value: 'Uzman Psikolog Danman', label: 'Uzman Psikolog Danman' },
  { value: 'ocuk ve Ergen Psikolou', label: 'ocuk ve Ergen Psikolou' },
  { value: 'Psikolog Danman', label: 'Psikolog Danman' },
  { value: 'Klinik Psikolog', label: 'Klinik Psikolog' },
  { value: 'Uzman Psikolog', label: 'Uzman Psikolog' }
];

const Filters = ({
  filters, updateList, fetchData, setOffset
}) => {
  const animatedComponents = makeAnimated();
  const history = useHistory();

  const [searchWord, setSearchWord] = useState(filters.isim || '');

  const getLabel = (options, key) => {
    let label = '';

    options.forEach(item => {
      if (item.value === key) {
        label = item.label;
      }
    });

    return label;
  };

  const getValue = key => {
    const value = [];

    let options = [];
    if (key === 'unvani') {
      options = tempUnvani;
    } else {
      options = tempUzmanlik;
    }
    if (filters[key]) {
      const values = filters[key].split(',');

      values.forEach(item => {
        value.push({
          value: item,
          label: getLabel(options, item)
        });
      });
    }

    return value;
  };

  const setFilter = (values, element) => {
    const searchParams = new URLSearchParams(history.location.search);

    let urlValue = '';
    if (Array.isArray(values)) {
      values.forEach(item => {
        urlValue += urlValue ? `,${item.value}` : `${item.value}`;
      });
    } else {
      urlValue = values.value;
    }

    if (urlValue) {
      searchParams.set(element?.name, urlValue);
    } else {
      searchParams.delete(element?.name);
    }

    history.push({
      pathname: '',
      search: searchParams?.toString()
    });

    const params = {
      uzmanlikAlani: searchParams.get('uzmanlikAlani') || null,
      unvani: searchParams.get('unvani') || null,
      isim: searchParams.get('isim') || null
    };

    setOffset(0);
    updateList();
    fetchData(false, params);
  };

  const handleSearch = searchKeyword => {
    const searchParams = new URLSearchParams(history.location.search);

    if (searchKeyword) {
      searchParams.set('isim', searchKeyword);
    } else {
      searchParams.delete('isim');
    }

    history.push({
      pathname: '',
      search: searchParams?.toString()
    });

    const params = {
      uzmanlikAlani: searchParams.get('uzmanlikAlani') || null,
      unvani: searchParams.get('unvani') || null,
      isim: searchParams.get('isim') || null
    };

    setOffset(0);
    updateList();
    fetchData(false, params);
  };

  const delayedSearch = useCallback(debounce(() => handleSearch(searchWord), 500), [searchWord]);

  useEffect(() => {
    delayedSearch();
    return delayedSearch.cancel;
  }, [searchWord, delayedSearch]);

  return (
    <div className='flex flex-row gap-4 max-w-lg pb-8 mx-auto'>
      <div className='w-full'>
        <span className='block pb-2 font-semibold'>Psikolog Ad</span>
        <input
          className='p-2 radius border border-gray-100 color-gray-700 w-full'
          type="text"
          value={searchWord}
          onChange={event => { setSearchWord(event.target.value); }}
          placeholder='sime gre ara'
        />
      </div>
      <div className='w-full'>
        <span className='block pb-2 font-semibold'>Uzmanlk Alan</span>
        <Select
          value={getValue('uzmanlikAlani')}
          closeMenuOnSelect={false}
          options={tempUzmanlik}
          components={animatedComponents}
          name="uzmanlikAlani"
          placeholder="Seiniz"
          onChange={setFilter}
          isMulti
        />
      </div>
      <div className='w-full'>
        <span className='block pb-2 font-semibold'>nvan</span>
        <Select
          value={getValue('unvani')}
          options={tempUnvani}
          onChange={setFilter}
          name="unvani"
          placeholder="Seiniz"
        />
      </div>
    </div>
  );
};

Filters.propTypes = {
  updateList: func,
  filters: object,
  fetchData: func,
  setOffset: func
};

Filters.defaultProps = {
  updateList: () => { },
  filters: {},
  fetchData: () => { },
  setOffset: () => { }
};

export default Filters;
